import { Col, Row } from 'antd';
import { Layout } from 'antd';
import * as React from 'react';
import { } from 'react-router';
import pic from '../../images/lauser.jpg'
import './UeberUns.scss';
// const { Header, Footer, Sider, Content } = Layout;

interface IProps {
  name: string;
  age?: number;
}

class UeberUns extends React.Component<IProps, object> {
  public render() {
    // let test = { titel: 'hallo' };
    // const test3 = test.titel;
    // test = test;
    // const ageX10 = this.props.age ? this.props.age * 10 : 0;

    return (
      <Layout className='container'>
        <Row type='flex' justify="center">
          <Col className='center' span={24}>
            <h1 style={{ color: '#D7A200' }}>ber uns</h1>

          </Col>
        </Row>
        <Row>

          <Col span={16}>
            <Row type='flex' >
              <div className='citeBox'>
                <p>Hunde faszinierten mich schon von klein auf. Als vor einigen Jahren die beiden
                Mischlinge Banya und rdg in mein Leben traten, wollte ich bei meinen ersten
              eigenen Hunden auf keinen Fall Fehler machen. Ich ging also regelmig in die
              Hundeschule, deckte mich mit Fachliteratur ein und nahm an einer Vielzahl von
              Kursen, Seminaren und Fachvortrgen diverser Hundetrainer und namhafter
              Hundeexperten teil. Nicht alles, was mir dabei begegnete, war fr mich stimmig.
              Starkzwangmethoden lehne ich rigoros ab, ich halte aber auch wenig davon,
                den Hund mit einem Berg von Leckerchen buchstblich an der Nase herumzufhren. </p>
                <br />
                <p>Dann hatte ich das groe Glck, auf den Schweizer Hundeexperten Hans Schlegel
                zu stoen. Er begeisterte mich bereits in meinem ersten Seminar bei ihm.
                Sein klarer, einfacher und vor allem herzlicher Umgang mit dem Partner Hund
                war die Antwort auf alle meine offenen Fragen und besttigte mich darin, mir
                endlich meinen langgehegten Wunsch zu erfllen und Mensch und Hund dabei zu
                helfen, auf der Basis einer vertrauens- und respektvollen Beziehung zu einem
                wirklichen Team zusammenzuwachsen, das vllig stressfrei jede Alltagssituation
                meistert.</p>
                <br />
                <p>
                  Banya und rdg untersttzen mich tagtglich bei dieser wundervollen Arbeit.
                  Hunde sind unsere besten Lehrmeister, die uns konsequent und bestndig jede
                  emotionale Schwche aufzeigen. Meinen beiden Lausern an dieser Stelle vielen Dank!
                </p>
              </div>
            </Row>
          </Col>

          <Col span={8}>
            <div>
              <img className='image35' src={pic} alt='bild' />
            </div>
          </Col>

        </Row>

      </Layout>
    );
  }
}

export default UeberUns;



